@media screen {
    body.print-component {
        width: auto;
        height: auto;
        max-width: 884px;
        padding: 12mm;
        margin: 20px auto;
        border: 1px dashed #c1c2c4;
    }
}

@media print {

    @page {
        size: 210mm 297mm;
        margin: 12mm 10mm;
    }

    .container {
        width: 1170px;
    }

    body.print-component {

        .form-footer,
        .dont-print,
        .actions-wrapper {
            @include force-hide;
        }

        &.print-black-color {
            color: #000000 !important;

            & * {
                color: #000000 !important;
            }
        }
    }

    .d-none {
        @include force-hide;
    }
}

