.pulse {
  position: relative;
}
.pulse:before {
  content: "";
  position: absolute;
  top: 4px;
  right: 4px;
  height: 20px;
  width: 20px;
  z-index: 10;
  border: 4px solid #ef5350;
  border-radius: 50%;
  animation: pulse 1s ease-out infinite;
}
.pulse:after {
  content: "";
  position: absolute;
  top: 10px;
  right: 10px;
  height: 8px;
  width: 8px;
  border-radius: 50%;
  background: red;
  z-index: 10;
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(0);
    opacity: 0;
  }
  25% {
    -webkit-transform: scale(0.1);
    opacity: 0.1;
  }
  50% {
    -webkit-transform: scale(0.5);
    opacity: 0.3;
  }
  75% {
    -webkit-transform: scale(0.8);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: scale(1);
    opacity: 0;
  }
}
